:root {
  color-scheme: light dark;
  scroll-behavior: smooth;
}

body {
  --tw-bg-opacity: 1;
  background-color: rgb(245 245 247 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(11 17 32 / var(--tw-text-opacity));
}

.dark body {
  --tw-bg-opacity: 1;
  background-color: rgb(5 7 17 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(229 231 235 / var(--tw-text-opacity));
}

h1,
h2,
h3,
h4 {
