}

* {
  scrollbar-width: thin;
  scrollbar-color: #9ca3af transparent;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #9ca3af;
  border-radius: 999px;
}

:focus-visible {
  outline: 2px solid #2563eb;
  outline-offset: 2px;
}

